.wrapper {
	position: fixed;
    top: 0;
    right: 0;
    left: 0;
    z-index: 1030;
}

.fa-stack[data-count]:after {
  position: absolute;
  right: 10%;
  top: 10%;
  content: attr(data-count);
  font-size: 30%;
  padding: .6em;
  border-radius: 999px;
  line-height: .75em;
  color: white;
  background: rgba(255, 0, 0, 0.85);
  text-align: center;
  min-width: 2em;
  font-weight: bold;
}
